import React from "react";
import { Col, Container, Row } from "react-bootstrap";
// needed styles
import styles from "./index.module.scss";
// needed components
// assets
import { ReactComponent as PhoneIcon } from "assets/svgs/phone.svg";
import { ReactComponent as ClockIcon } from "assets/svgs/clock.svg";
import { ReactComponent as LocationIcon } from "assets/svgs/location.svg";
import { FormattedMessage, useIntl } from "react-intl";
import { useForm } from "react-hook-form";
import { sendUserComplaint } from "api/compliants";
import { toast } from "react-toastify";

const ContactUsPage = () => {
  const { formatMessage } = useIntl();
  const data = {
    address: {
      name: "عنوان",
      content:
        "    عنوان VisaHQ.com.eg ٥٢ شارع عباس العقاد   عنوان VisaHQ.com.eg ٥٢ شارع عباس العقاد",
    },
    phone: {
      name: "عنوان",
      content:
        "    عنوان VisaHQ.com.eg ٥٢ شارع عباس العقاد   عنوان VisaHQ.com.eg ٥٢ شارع عباس العقاد",
    },
    time: {
      name: "عنوان",
      content:
        "    عنوان VisaHQ.com.eg ٥٢ شارع عباس العقاد   عنوان VisaHQ.com.eg ٥٢ شارع عباس العقاد",
    },
    email: {
      name: "عنوان",
      content: "   HunterXHunter ",
    },
  };

  const { register, handleSubmit, reset } = useForm({
    shouldUseNativeValidation: true,
  });

  const submitHandler = async (data) => {
    try {
      const response = await sendUserComplaint(data);
      toast.success(formatMessage({ id: "success-messages" }));
    } catch (error) {
      toast.error(error.message);
    }
  };

  return (
    <Container>
      <Row className={styles["row-styles"]}>
        <Col xs={12} lg={6}>
          <h1 className={styles["header"]}>معلومات التواصل</h1>
          <div className={styles["section"]}>
            <div className={`${styles["flex"]}`}>
              <span>
                <LocationIcon />
              </span>
              <h4>{data?.address?.name}</h4>
            </div>
            <p>{data?.address?.content}</p>
          </div>
          <div className={styles["section"]}>
            <div className={`${styles["flex"]}`}>
              <span>
                <PhoneIcon />
              </span>
              <h4>{data?.phone?.name}</h4>
            </div>
            <p>{data?.phone?.content}</p>
          </div>
          <div className={styles["section"]}>
            <div className={`${styles["flex"]}`}>
              <span>
                <ClockIcon />
              </span>
              <h4>{data?.time?.name}</h4>
            </div>
            <p>{data?.time?.content}</p>
          </div>
          <div className={styles["section"]}>
            <h5>{data?.email.name}</h5>
            <p>{data?.email?.content}</p>
          </div>
        </Col>
        <Col xs={12} lg={6}>
          <div style={{ width: "100%" }}>
            <iframe
              title="contact-us-map"
              width="100%"
              height="300"
              frameBorder="0"
              scrolling="no"
              marginHeight="0"
              marginWidth="0"
              src="https://maps.google.com/maps?width=100%25&amp;height=600&amp;hl=en&amp;q=1%20Grafton%20Street,%20Dublin,%20Ireland+(My%20Business%20Name)&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"
            >
              <a href="https://www.gps.ie/marine-gps/">marine gps</a>
            </iframe>
          </div>
        </Col>
      </Row>
      <Row className="my-3">
        <form onSubmit={handleSubmit(submitHandler)} className={styles.Form}>
          <h3 className="mb-3">
            <FormattedMessage id="any-complaints" />
          </h3>

          <label htmlFor="phone">
            <input
              type="number"
              name="phone"
              id="phone"
              placeholder="رقم الهاتف"
              {...register("phone", { required: true })}
            />
            <div></div>
          </label>
          <label htmlFor="text">
            <textarea
              cols={15}
              rows={15}
              name="text"
              id="text"
              placeholder="ملاحظات"
              {...register("text", { required: false })}
            />
          </label>

          <input type="submit" value="ارسال" />
        </form>
      </Row>
    </Container>
  );
};

export default ContactUsPage;
